import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/returnReason/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class ReturnReason extends Component {
  state = {
    filter: {
      //id_cash_flow_group: [],
      transaction_type: [],
      pagination: null
    },
    search: null,
    transaction_types: [
      {
        id: 1,
        name: "All"
      },
      {
        id: 2,
        name: "Purchase"
      },
      {
        id: 3,
        name: "Sales"
      }
    ]
  };

  // Get All data diawal
  componentDidMount() {
    const { getReturnReasons } = this.props;
    getReturnReasons();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.returnReason.error) {
      notification("error", this.props.returnReason.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.returnReason.success) {
      notification("success", this.props.returnReason.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    console.log(value);
    const { getReturnReasons } = this.props;
    this.setState({
      filter: {
        //id_cash_flow_group: [],
        transaction_type: [],
        pagination: null
      }
    });
    getReturnReasons(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getReturnReasons } = this.props;
    this.setState({
      filter: {
        id_cash_flow_group: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getReturnReasons();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getReturnReasons } = this.props;
    //const { current_page } = this.props.returnReason;
    const { filter } = this.state;
    let string = "";

    if (filter.transaction_type) {
      string += "transaction_type=" + filter.transaction_type;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getReturnReasons(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getReturnReasons } = this.props;
    this.setState({
      filter: {
        //id_cash_flow_group: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getReturnReasons();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateReturnReason(id);
    } else {
      this.props.deactivateReturnReason(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getReturnReasons } = this.props;
    const { search } = this.props.returnReason;
    const { filter } = this.props.returnReason;
    getReturnReasons(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_return_reason",
    rowKey: "id_return_reason",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="returnReasons.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText="Yes"
            cancelText="No"
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="returnReasons.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText="Yes"
            cancelText="No"
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "return_reason_code",
        rowKey: "return_reason_code",
        width: "30%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "users.add.description" }),
        dataIndex: "description",
        rowKey: "description",
        width: "50%",
        render: display => {
          return display.length > 50
            ? display.substring(0, 50) + "..."
            : display;
        }
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type",
        rowKey: "transaction_type",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = getLanguage({ id: "All" });
          } else if (text === 2) {
            className = "active";
            display = getLanguage({ id: "Purchase" });
          } else {
            className = "warning";
            display = getLanguage({ id: "Sales" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // Data setup
    const { return_reasons } = this.props.returnReason;
    //console.log(this.props.returnReason); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Return Reason") && columns.length === 4) {
      columns.push(this.listAction);
    }

    Object.keys(return_reasons).map((returnReason, index) => {
      return dataSource.push({
        ...return_reasons[returnReason],
        key: returnReason
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.returnReason;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="returnReasons.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Return Reason") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="returnReasons.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) +
                ", " +
                getLanguage({ id: "label.description" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "20%" }}>
                    <label>
                      <IntlMessages id="transactionType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "transaction_type"
                      )}
                      value={
                        this.state.filter.transaction_type
                          ? this.state.filter.transaction_type
                          : []
                      }
                      style={{ width: "90%" }}
                      placeholder={
                        <IntlMessages id="returnReasons.filter.select.transactionType" />
                      }
                      allowClear={true}
                      showArrow={true}
                    >
                      {this.state.transaction_types.map((data, key) => (
                        <Option key={key} value={data.id}>
                          {getLanguage({id: data.name})}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "70%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.returnReason.loading}
                      // allowClear={true}
                      // showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "20%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {return_reasons.length === 0 && !this.props.returnReason.loading ? (
              <Empty
                description={<IntlMessages id="returnReasons.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.returnReason.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    returnReason: state.returnReason,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(ReturnReason);
